










import Vue from 'vue';
export default {
	props: {
		food: Object
	},
	data() {
		return {
			add_icon: '../../../static/shop/icon_add.png',
			minus_icon: '../../../static/shop/icon_minus.png'
		}
	},
	methods: {
		addCart(event) {
			if (!this.food.count) {
				Vue.set(this.food, 'count', 1); // 向对象/数组添加是非响应的，强制响应
			} else {
				this.food.count++;
			};
			this.$emit('add', event.target); // 触发当前实例上的事件，以便父元素@监听子元素。将点击的元素传入
		},
		decreaseCart() {
			if (this.food.count) {
				this.food.count--;
			}
		}
	}

}
